<template lang="pug">
    div
        //- router-view(style='padding-bottom: 1.6rem;') //- 底部选项卡浮动占位。*待优化：cube-ui 采用Sticky组件
        cube-tab-bar(
            v-model="selectedLabelDefault"
            :data="tabs"
            @click="clickHandler"
            @change="changeHandler"   
        )
</template>

<script>
import { isEmpty } from "@/utils";
export default {
  props: {
    prop: {
      tab: {}
    }
  },
  data() {
    return {
      transitionName: "slide-right",
      // selectedLabelDefault: '首页',
      selectedLabelDefault: "",
      tabs: [
        {
          label: "首页",
          icon: "cubeic-home",
          path: "/project/list"
        },
        {
          label: "我的",
          icon: "cubeic-person",
          path: "/user/mine"
        }
      ]
    };
  },
  watch: {
    $route(to, from) {
      this.changeHandlerByPath(to.path);
      // if(to.path !== from.path){
      //     this.changeHandlerByPath(to.path);
      // }
    }
  },
  methods: {
    clickHandler(label) {
      // if you clicked home tab, then print 'Home'
      this.changeHandler(label);
    },
    // 点击与自身不同的其他导航
    changeHandler(label) {
      // if you clicked different tab, this methods can be emitted
      switch (label) {
        case "首页":
          this.$router.push({ path: "/project/list" });
          break;
        case "我的":
          this.$router.push({ path: "/user/mine" });
          break;
        case "测试":
          this.$router.push({ path: "/test" });
          break;
      }
    },
    changeHandlerByPath(path) {
      switch (path) {
        case "/project/list":
          this.selectedLabelDefault = "首页";
          break;
        case "/user/mine":
          this.selectedLabelDefault = "我的";
          break;
        case "/test":
          this.selectedLabelDefault = "测试";
          break;
        default:
          this.selectedLabelDefault = "";
      }
    }
  },
  created() {
    this.changeHandlerByPath(this.$route.path);
    const tab = this.prop.tab;
    if (!isEmpty(tab)) {
      this.tabs.push(tab);
    }
  },

  mounted() {
    document.body.style.marginBottom = "100px";
  },
  beforeDestroy() {
    document.body.style.marginBottom = "60px";
  }
};
</script>

<style lang="stylus" scoped>
.cube-tab-bar
  position fixed
  bottom 0
  left 0
  // 藏于蒙版之下，蒙版为100层
  z-index 99
  width 100%
  background #fff
  height 50px
  /deep/ .cube-tab
    &>div
      font-size 12px
      padding-top 3px
      padding-bottom 24px
    &>i
      font-size 14px
</style>
